.field
{
   position: relative;

   margin-top: 25px;

   &--btn
   {
      margin-top: 35px;
   }

   &__label
   {
      position: absolute;
      top: 5px;
      left: 0;

      font-size: .625em;

      transition: opacity .2s ease, color .2s ease, top .2s ease;
      pointer-events: none;

      opacity: 0;
      color: #a0a0a0;

      &--active
      {
         top: -6px;

         opacity: 1;
         color: $primaryColor;
      }

      &--error
      {
         color: $errorColor;
      }
   }

   &__input
   {
      display: block;

      font-family: "Roboto";
      line-height: 40px;

      width: 100%;
      height: 40px;
      padding: 0;

      transition: border-color .2s ease;

      border-width: 0 0 1px 0;
      border-style: solid;
      border-color: #a0a0a0;
      outline: none;
      background: #fff;

      &--active
      {
         border-color: $primaryColor;
      }

      &--error
      {
         border-color: $errorColor;
      }
   }

   &__pwd-show
   {
      position: absolute;
      z-index: 10;
      top: 13px;
      right: 0;

      width: 23px;
      height: 17px;

      cursor: pointer;

      background-image: url(../img/icons/pwd-show.svg);
      background-repeat: no-repeat;
      background-position: center;

      &--active
      {
         background-image: url(../img/icons/pwd-show--a.svg);
      }
   }

   &__error
   {
      font-size: .75em;

      padding-top: 8px;

      text-align: left;

      color: $errorColor;
   }
}
