::-webkit-input-placeholder
{
   color: #a0a0a0;
}

::-moz-placeholder
{
   color: #a0a0a0;
}

:-ms-input-placeholder
{
   color: #a0a0a0;
}

:-moz-placeholder
{
   color: #a0a0a0;
}

body
{
   font-family: "Roboto";

   color: #363636;
   background: #fff;
}

a
{
   text-decoration: none;

   color: $primaryColor;

   &:hover
   {
      text-decoration: underline;
   }
}

.fancybox-is-open .fancybox-bg
{
   opacity: .85;
   background-color: #000;
}

.fancybox-is-open .fb-bg
{
   width: 100%;
   height: 100%;

   background-color: #262626;
}
