.demo-wrapper
{
   position: relative;

   height: 100vh;

   &-bg
   {
      background: url(../img/temp.jpg) no-repeat top center / cover;
   }
}

.demo-bar
{
   position: absolute;
   top: 0;
   left: 0;

   display: flex;

   line-height: 50px;

   width: 100%;
   height: 50px;
   padding: 0 40px;

   background: #fff;

   &-item
   {
      display: block;
      margin-right: 20px;
   }
}
