.popup
{
   position: relative;

   display: none;

   width: 100%;
   max-width: 400px;
   margin: 0;
   padding: 0;

   border-radius: 4px;
   background: #fff;

   &__caption
   {
      font-size: 1.5em;

      text-align: center;

      color: #191a39;
   }

   &__sub-caption
   {
      margin-top: 20px;

      text-align: center;

      color: #191a39;
   }

   &__close
   {
      position: absolute;
      top: 6px;
      right: -36px;

      width: 17px;
      height: 17px;

      cursor: pointer;
      transition: opacity .3s ease;

      background: url(../img/icons/popup__close.svg) no-repeat center / contain;

      &:hover
      {
         opacity: .8;
      }
   }

   &__inner
   {
      padding: 40px 35px;
   }

   &__text
   {
      line-height: 1.3;

      margin-top: 20px;

      text-align: center;

      color: #4b4b4b;
   }

   &__hr
   {
      width: 100%;
      height: 1px;
      margin-top: 15px;

      background: #ececec;
   }

   &__bottom-links-row
   {
      display: flex;
      justify-content: space-between;

      font-size: .75em;

      margin-top: 35px;
   }

   &__bottom-link
   {
      &--back
      {
         &::before
         {
            display: inline-block;

            width: 9px;
            height: 7px;
            margin-right: 5px;

            content: "";

            background: url(../img/icons/back-arrow.svg) no-repeat center / contain;
         }
      }
   }

   &__bottom
   {
      font-size: .75em;
      line-height: 1.5;

      margin-top: 30px;

      text-align: center;

      color: #bfbfbf;

      &-row
      {
         margin-top: 15px;
      }
   }

   &__privacy
   {
      &::before
      {
         display: inline-block;

         width: 16px;
         height: 14px;
         margin-right: 5px;

         content: "";
         vertical-align: bottom;

         background: url(../img/icons/privacy-cbx.svg) no-repeat center / contain;
      }
   }
}
