.btn
{
   display: block;

   font-family: "Roboto";
   line-height: 45px;

   width: 100%;
   height: 45px;
   padding: 0;

   cursor: pointer;
   transition: background-color .3s ease, box-shadow .3s ease;
   text-align: center;

   color: #fff;
   border: none;
   border-radius: 50px;
   outline: none;
   background-color: $primaryColor;
   background-image: url(../img/elems/btn-waves.svg);
   background-repeat: no-repeat;
   background-position: center;
   background-size: cover;

   &:hover
   {
      background-color: $primaryColorH;
   }

   &:hover:active
   {
      box-shadow: inset 0 2px 0 #004095;
   }

   &:focus
   {
      box-shadow: inset 0 0 0 1px #004095;
   }

   &--form
   {
      max-width: 225px;
      margin: 0 auto;
   }

   &--error
   {
      background-color: $errorColor;

      &:hover
      {
         background-color: $errorColor;
      }

      &:hover:active
      {
         box-shadow: none;
      }

      &:focus
      {
         box-shadow: none;
      }
   }
}
